footer {
  background: var(--color-primary);
  padding: 2rem 0 7rem;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-bg);
  line-height: 1.5rem;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.footer__socials {
  margin-top: 0.5rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
}

/* ===== MEDIA QUERIES (SMALL DEVICES) ===== */
@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1rem;
  }
}
